import React from 'react'
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { useState } from 'react';
import Items from './Items';
import Receipt from './Receipt';
import ToggleButton from './ToggleButton';

const Body = () => {

    const[items, setItems] = useState(Items);

    const[total, setItem] = useState(Receipt);

    return (
        <div className="App-body">
            <body>
                <form className="add-form" action="#" method="post">
                    
                    <div onClick="{addAddress}"  className="card">
                        <h1><b>Delivery Address</b><ArrowForwardIosIcon style={{marginLeft:"320px"}}/></h1>
                        <p className="faded">Please Choose destination address.</p>
                    </div>
                    
                    <div className="card">
                        <label style={{fontSize:"2em"}} for="flexSwitchCheckChecked">Dropship</label>
                        <ToggleButton/>
                    </div>
                    
                    <div onClick="{addShipping}" className="card">
                        <h1><b>Shipping Option</b><ArrowForwardIosIcon style={{marginLeft:"320px"}}/></h1>
                        <p className="faded">Please Choose your shipping method.</p>
                    </div>
                    
                    <div onClick="{addPayment}" className="card">
                        <h1><b>Payment Option</b><ArrowForwardIosIcon style={{marginLeft:"320px"}}/></h1>
                        <p className="faded">Please Choose your payment method.</p>
                    </div>

                    <div className="card">
                        <h1><b>Order Details</b></h1>
                        {
                            items.map((elem) => {
                                const{ id, image, name, varient, size, quantity, weight, mrp} = elem;
                                return (
                                <div className="outerDiv my-5">
                                    <div style={{width:"30%"}} className="leftDiv">
                                        <img src={image} alt="item-img" className="img-fluid"/>
                                    </div>
                                    <div style={{width:"70%"}} className="rightDiv">
                                        <h1>{name}</h1>
                                        <div style={{backgroundColor:"grey",color:"white",borderRadius:"5px",width:"250px"}} ><h1 style={{marginLeft:"3%"}}>Varient: {varient},{size}</h1></div>
                                        <h1>{quantity} PCS({weight}gr)@ RS. {mrp}</h1>
                                    </div>
                                    <input type="text" class="no-outline" placeholder="Optional Message Here." id="optionalMessage" />
                                    <hr/>
                                </div>
                                )
                            })
                        }
                    </div>

                    <div className="card">
                        <div className="outerDiv">
                            <div className="leftDiv"><img style={{height:"30px"}} src="images/voucher.png" alt="item-img" className="img-fluid"/><input style={{width:"70%", fontSize:"30px", marginLeft:"10px"}} type="text" className="no-outline" placeholder="Voucher Code" id="voucherCode"/></div>
                            <div><h1>Apply</h1></div>
                        </div>
                        <hr/>
                    </div>

                    <div style={{height:"210px"}} className="card">

                        {
                            total.map((e) => {
                                const{id, name, value} = e;
                                return(
                                <div className="outerDiv my-5">
                                    <div className="leftDiv">
                                        <b>{name}</b>
                                    </div>
                                    <div className="rightDiv">
                                        <b>{value}</b>
                                    </div>
                                </div>
                                )
                            })
                        }

                    </div>

                    <div class="form-check checkbox">
                        <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
                        <label class="form-check-label" for="flexCheckDefault">
                        <span style={{fontSize:"20px"}}> With Payment, I agree terms and condition.</span>
                        </label>
                    </div>

                    <div class="btn-group">
                        <button type="submit" class="btn">FINISH</button>
                    </div>

                </form>
            </body>
        </div>
    )
}

export default Body
